import { StudentsInterface } from './../../services/students-interface';
import { element } from 'protractor';
import { FiribaseServiceService } from './../../services/firibase-service.service';
import { Component, OnInit } from '@angular/core';
import { from } from 'rxjs';
import {NgForm} from '@angular/forms'
import { MatDialog } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  students :any
  exixt : any
  number: any
  constructor( public _data : FiribaseServiceService,public dialog: MatDialog, private toastr : ToastrService) { }

  AddStudents(StudentsData: NgForm){

   


    this._data.AddStudent(StudentsData.value);
    this.toastr.success('Registred Sucessfully','Students',{timeOut: 9000,});
    StudentsData.form.reset();

   }


  ngOnInit(){

      this._data.viewStudends().snapshotChanges().subscribe( action =>{
        console.log(action)
        this.students=action
        this.exixt= action

        console.log("Length"+action.length)

        this.number=action.length;
      })
  }


}
